<template>
  <div v-loading="isLoading">

    <section class="form-list" v-if='formList.length'>
      <div class="form-area">
        <el-button :type="index==activeMenuIndex?'primary':''" class="form-btn" size="mini" @click="jumpPageForm(item,index)" v-for="(item,index) in formList" :key="index">
          {{item.formname}}
        </el-button>
      </div>
    </section>
    <section v-if='formList.length===0&&!isLoading'>
      <el-button type="primary" class="big-btn" icon='el-icon-plus' @click="jumpPageEditForm">
        添加表单
      </el-button>
    </section>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        formList: [],
        activeMenuIndex: -1,
        isLoading: true,
      }
    },

    created() {
      this.getFormList();
    },

    methods: {

      async getFormList() {
        let data = await this.$http.post('/form/getFormList', {
          type: 1
        })
        this.formList = data.data
        this.$nextTick(() => {
          this.isLoading = false
        })
        if (this.formList && this.formList.length) {
          Object.assign(this.$route.query, {
            id: this.formList[0].id
          })
        } else {
          // location.href = '/'
          this.$router.push({
            path: '/'
          })
        }
      },
      jumpPageForm(item, index) {
        this.activeMenuIndex = index;
        this.$router.push({
          path: '/user/output-release',
          query: {
            id: item.id
          }
        })
      },

      jumpPageEditForm() {
        this.$router.push({
          path: '/admin/editForm',
        })
      },
    }
  }
</script>

<style lang="less" scoped>

</style>


